@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400&display=swap);
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

:root {
    --primary-color: #FAF3E0;
    --second-color: #EABF9F;
    --light-color: #B68973;
    --dark-color: #1E212D;
}

body {
    background-color: #FAF3E0;
    background-color: var(--primary-color);
    font-family: 'Poppins', sans-serif;
}

main {
    width: 90%;
    max-width: 30rem;
    margin: 3rem auto;
}

p {
    color: #B68973;
    color: var(--light-color);
    font-size: 1rem;
    text-align: center;
}

#App_todos__1KsdR {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.Header_header__1LS3J {
    display: grid;
    grid-template-columns: 1fr 2fr;
    grid-template-areas: "main second"
                         "main third";
    place-items: center;
    margin: 2rem 0;
}

h1 {
    color: var(--dark-color);
    font-size: 3rem;
    text-align: center;
    grid-area: second;
}

img {
    height: 10rem;
    grid-area: main;
}

h3 {
    color: var(--light-color);
    font-size: 1.3rem;
    text-align: center;
    grid-area: third;
}

.Actions_filters__2i-_U {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 2rem;
}

.Actions_filters__2i-_U button {
    font-family: 'Poppins', sans-serif;
    background: var(--second-color);
    font-size: 0.9rem;
    border: none;
    border-radius: 0.3rem;
    padding: 0.4rem;
    outline: none;
    cursor: pointer;
}

input[type="text"] {
    display: block;
    width: 100%;
    border: 0.15rem solid var(--second-color);
    border-radius: 0.3rem;
    font-family: 'Poppins', sans-serif;
    outline: none;
    font-size: 1rem;
    padding: 0.1rem 0.3rem;
}

.Formulario_containerButton__1H5UV {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 1rem 0;
    width: 100%;
}

form button {
    display: block;
    width: 80%;
    outline: none;
    font-family: 'Poppins', sans-serif;
    background: var(--light-color);
    color: white;
    text-transform: uppercase;
    font-size: 1rem;
    border: none;
    border-radius: 0.3rem;
    padding: 0.3rem 0;
    cursor:pointer;
}

.Todo_todo__1zueQ {
    padding: 0.5rem;
    font-size: 1rem;
    display: grid;
    grid-template-columns: 0.2fr 5fr 1.3fr 1.3fr;
    grid-gap: 1rem;
    gap: 1rem;
    -webkit-align-items: center;
            align-items: center;
}

.Todo_todo__1zueQ button {
    font-family: 'Poppins', sans-serif;
    background: var(--second-color);
    font-size: 0.8rem;
    border: none;
    padding: 0.4rem;
    border-radius: 0.3rem;
    outline: none;
    cursor: pointer;
}

.Todo_todo__1zueQ button:nth-child(4) {
